
.company_name {
	font-family: 'Fjalla One';
	padding: 5px;
	font-size: 40px;
	font-weight: 800;
}

.tagline {
	padding: 5px;
	font-size: 28px;
	font-weight: 600;
}

.myhero {
	padding: 0;
	margin: 0;
	 background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.46)), to(rgba(0, 0, 0, 0.46))), url('https://source.unsplash.com/XJXWbfSo2f0/2000x1200');
	 background: -webkit-linear-gradient(rgba(0, 0, 0, 0.46), rgba(0, 0, 0, 0.46)), url('https://source.unsplash.com/XJXWbfSo2f0/2000x1200');
	 background: -o-linear-gradient(rgba(0, 0, 0, 0.46), rgba(0, 0, 0, 0.46)), url('https://source.unsplash.com/XJXWbfSo2f0/2000x1200');
	 background: linear-gradient(rgba(0, 0, 0, 0.46), rgba(0, 0, 0, 0.46)), url('https://source.unsplash.com/XJXWbfSo2f0/2000x1200');
	color: white;
}

.three_col {
   display: -ms-flexbox;
   display: flex;
   -ms-flex-direction: row;
       flex-direction: row;
   -ms-flex-align: center;
       align-items: center;
   -ms-flex-pack: center;
       justify-content: center;
}

.feature_box {
	width: 300px;
	padding-left: 10px;
	padding-right: 10px;
}

.feature_box h3{
	text-align: center;
}

.icon-header{
	display: block;
	padding-bottom: 15px;
}

.divider {
	font-size: 38px;
	padding-top: 50px;
	padding-bottom: 50px;
}

.footer {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0; 
  padding: 1rem;
  background-color: #efefef;
  text-align: center;
}

.callToAction2 {
	padding-top: 10px;
	text-align: center;
	padding-bottom: 150px;
}


/* Responsive */
@media only screen and (max-width: 768px) {
    /* For mobile phones: */
    .three_col {
    	display: block;
    	text-align: center
    }

    .feature_box {
    	margin: auto;
    	padding-bottom: 35px;
    }
}


